<template>
  <div class="arsti">
    <div class="row saraksts" v-show="mode == 0">
      <div class="col-md-8 offset-2">
        <table class="table table-bordered">
          <thead>
            <tr>
              <th style="width: 25px"></th>
              <th>Pilns vārds</th>
              <th>Personas kods</th>
            </tr>
          </thead>
          <tr v-for="(item, index) in arsti" :key="index">
            <td>
              <b-button variant="danger" @click="deleteCheck(item)">Dzēst</b-button>
            </td>
            <td>{{ item.vards + " " + item.uzvards }}</td>
            <td>{{ item.pk }}</td>
          </tr>
          <tr v-if="arsti == undefined || arsti == null">
            <td colspan="3">Nav datu</td>
          </tr>
        </table>
        <b-button class="submit-button" @click="mode = 1">Pievienot sertificēto ārstu</b-button>
      </div>
    </div>
    <div class="row meklet" v-show="mode == 1">
      <div class="col-md-8 offset-2">
        <b-form @submit="onSubmit">
          <b-form-group id="input-group-code" label="Personas kods:" label-for="input-code-one">
            <b-form-input
              id="input-code-one"
              placeholder="123456-12345"
              v-mask="'######-#####'"
              v-model="pers_kods"
              maxlength="12"
              required
              oninvalid="this.setCustomValidity('Lūdzu aizpildiet šo lauku.')"
              onchange="this.setCustomValidity('')"
            ></b-form-input>
          </b-form-group>
          <b-button type="submit" class="submit-button">Meklēt</b-button>
        </b-form>
      </div>
    </div>
    <div class="row saraksts" v-show="mode == 2">
      <div class="col-md-8 offset-2">
        <table class="table table-bordered" v-if="person !== null">
          <thead>
            <tr>
              <th>Pilns vārds</th>
              <th>Personas kods</th>
            </tr>
          </thead>
          <tr>
            <td>{{ person.vards + " " + person.uzvards }}</td>
            <td>{{ person.pk }}</td>
          </tr>
          <tr v-if="person == null">
            <td colspan="3">Nav datu</td>
          </tr>
        </table>
        <b-button class="submit-button" @click="add">Pievienot</b-button>
        <b-button variant="danger" @click="cancel">Atcelt</b-button>
      </div>
    </div>
  </div>
</template>

<script>
import toast from "../services/toast";
import TheMask from "vue-the-mask";
import "izitoast/dist/css/iziToast.min.css";
import iZiToast from "izitoast";

export default {
  name: "arsti",
  components: {
    TheMask,
  },
  data() {
    return {
      arsti: null,
      pers_kods: null,
      mode: 0,
      person: null,
    };
  },
  methods: {
    deleteCheck(item) {
      var vue = this;
      iZiToast.question({
        timeout: 20000,
        close: false,
        overlay: true,
        displayMode: "once",
        id: "question",
        zindex: 999,
        title: "Dzēst",
        message:
          "Vai jūs tiešām vēlaties dzēst šo personu no sertificēto ārstu saraksta?",
        position: "center",
        buttons: [
          [
            "<button><b>Jā</b></button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
              vue.delete(item);
            },
          ],
          [
            "<button>Nē</button>",
            function (instance, toast) {
              instance.hide({ transitionOut: "fadeOut" }, toast, "button");
            },
          ],
        ],
      });
    },
    delete(item) {
      axios
        .post("/api/arsts-dzest", {
          sessionId: sessionStorage.getItem("sessionId"),
          id: item.id,
        })
        .then((response) => {
          console.log(response);
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          } else {
            toast.success("Sertificētais ārsts dzēsts veiksmīgi.");
            this.mode = 0;
            this.load();
            console.log(response);
          }
        })
        .catch((error) => console.log(error));
    },
    add() {
      axios
        .post("/api/arsts-jauns", {
          sessionId: sessionStorage.getItem("sessionId"),
          gprs_id: this.person.id,
        })
        .then((response) => {
          console.log(response);
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          } else {
            toast.success("Sertificētais ārsts pievienots veiksmīgi.");
            this.mode = 0;
            this.load();
            console.log(response);
          }
        })
        .catch((error) => console.log(error));
    },
    cancel() {
      this.mode = 0;
      this.pers_kods = null;
      this.person = null;
    },
    persCode() {
      return this.pers_kods.replace("-", "");
    },
    onSubmit() {
      event.preventDefault();

      axios
        .post("/api/personas-dati", {
          sessionId: sessionStorage.getItem("sessionId"),
          pers_kods: this.persCode(),
          pap_dati: false,
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          } else {
            this.person = response.data.rowset[0];
            this.mode = 2;
          }
        })
        .catch((error) => console.log(error));
    },
    load() {
      axios
        .post("api/arstu-saraksts", {
          sessionId: sessionStorage.getItem("sessionId"),
        })
        .then((response) => {
          if (response.data.Errors !== undefined) {
            toast.error(response.data.Errors[0].ClientMessage);
          } else {
            this.arsti = response.data.rowset;
          }
        })
        .catch((error) => console.log(error));
    },
  },
  mounted() {
    this.load();
  },
};
</script>